import { useEffect, useState } from 'react';

import { finUUID } from '@common/utils/env';
import { showError, showSuccess } from '@common/utils/indicators/showIndicator';
import { getCurrentOFWindow } from '@common/utils/window';

import { t, useTranslation } from '@common/api/i18next';
import { cleanPage } from '@common/api/pages';
import { copyPage } from '@common/api/pages/internal';

import { getCurrentSync } from '@client-platform/api';
import { AttachedPage, BrowserWindowModule, Page } from '@client-platform/index';

import SaveModal from './SaveModal';

const savePage = async (
    title: string,
    page: AttachedPage | Page,
    browser: BrowserWindowModule,
    isEnterprise: boolean
) => {
    page.title = title;
    await getCurrentSync().Storage.savePage(cleanPage(page));
    // disables unsaved changes icon in browser
    await browser.updatePage({
        pageId: page.pageId,
        page: {
            hasUnsavedChanges: false
        }
    });
    await showSuccess({
        message: isEnterprise ? t('enterpriseSuccessIndicator.supertabSaved') : t('successIndicator.pageSaved'),
        parentBrowserName: browser.identity.name,
        secondaryMessage: isEnterprise
            ? t('enterpriseSuccessIndicator.supertabHasBeenSaved', { title })
            : t('successIndicator.pageHasBeenSaved', { title }),
        isEnterprise
    });
};

const savePageCopy = async (
    title: string,
    page: AttachedPage | Page,
    browser: BrowserWindowModule,
    isEnterprise: boolean
) => {
    // remove view names, add new id
    const clonedPage = await copyPage({ page, reason: 'save-as', identity: browser.identity });
    // remove parent identity, unsaved changes flag
    const cleanedPageCopy = { ...cleanPage(clonedPage), title };
    await getCurrentSync().Storage.savePage(cleanedPageCopy);

    // Ignore layout changes and panel changes if received from override
    delete clonedPage.layout;
    delete clonedPage.panels;
    await browser.updatePage({
        pageId: page.pageId,
        page: {
            ...clonedPage,
            title,
            hasUnsavedChanges: false
        }
    });
    await showSuccess({
        message: isEnterprise
            ? t('enterpriseSuccessIndicator.supertabSaved')
            : t('successIndicator.pageCopySaved', { title }),
        parentBrowserName: browser.identity.name,
        secondaryMessage: isEnterprise
            ? t('enterpriseSuccessIndicator.supertabHasBeenSaved', { title })
            : t('successIndicator.pageCopySaved', { title }),
        isEnterprise
    });
};

const renamePage = async (newTitle: string, page: AttachedPage | Page, browser: BrowserWindowModule) => {
    const platform = await getCurrentSync();
    // only save new title, not the page internals
    const savedPage = await platform.Storage.getPage(page.pageId);
    await platform.Storage.updatePage({
        pageId: page.pageId,
        page: {
            ...savedPage,
            title: newTitle
        }
    });
    await browser.updatePage({
        pageId: page.pageId,
        page: {
            title: newTitle
        }
    });
    await showSuccess({
        message: t('successIndicator.pageRenamed'),
        parentBrowserName: browser.identity.name
    });
};

interface Props {
    saveAs?: boolean;
    rename?: boolean;
    pageId: string;
    parentWindowName: string;
    shouldClose?: boolean;
    modalInvocationId?: number;
    isEnterprise: boolean;
}
const SavePage = ({ parentWindowName, pageId, saveAs, rename, modalInvocationId, isEnterprise }: Props) => {
    const [pageTitle, setPageTitle] = useState('');
    const [modalTitle, setModalTitle] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const { t: tt } = useTranslation();

    // clear error message on cancel
    const handleCancel = async () => {
        setErrorMessage('');
    };

    const handleSubmit = async (title: string) => {
        console.log('%chandling submit, initializing variables....', 'color: purple');
        const allSavedPages = await getCurrentSync().Storage.getPages();
        const savedPageWithTitle = allSavedPages?.find((p) => p?.title === title);
        const isTitleExists = savedPageWithTitle && savedPageWithTitle.pageId !== pageId;
        const modalWindow = getCurrentOFWindow();

        console.log('%cupdating modal windows otpions...', 'color: purple');
        await modalWindow.updateOptions({ customContext: isTitleExists ? 'error' : 'submitting' });

        if (isTitleExists) {
            setErrorMessage(tt('saveModal.alreadyExists', { title }));
            return;
        }

        console.log('%chiding modal...', 'color: purple');
        await modalWindow.hide();
        try {
            console.log('%cgetting platform and the browser...', 'color: purple');
            const platform = getCurrentSync();
            const browser = platform.Browser.wrapSync({ name: parentWindowName, uuid: finUUID });
            let page: Page | AttachedPage = await browser.getPage(pageId);
            if (!page) {
                console.log(
                    "%cpage isn't attached to current window (e.g. Rename called from landing page)",
                    'color: purple'
                );
                // page isn't attached to current window (e.g. Rename called from landing page)
                page = await platform.Storage.getPage(pageId);
            }

            let shouldSaveCopy = saveAs;
            if (shouldSaveCopy && page.title === title) {
                // if we are in Save As modal but receive current page name then save without creating a copy
                shouldSaveCopy = false;
            }

            if (shouldSaveCopy) {
                // Save As
                console.log('%csavePageCopy', 'color: purple');
                await savePageCopy(title, page, browser, isEnterprise);
            } else if (rename) {
                console.log('%cranme', 'color: purple');
                await renamePage(title, page, browser);
            } else {
                // regular save
                console.log('%cregular...', 'color: purple');
                await savePage(title, page, browser, isEnterprise);
            }
        } catch (err) {
            console.error('SavePage: page failed to save: ', err);
            showError({
                message: tt('errorIndicator.pageSaveFailed'),
                parentBrowserName: parentWindowName
            });
        }
    };

    useEffect(() => {
        (async () => {
            if (isEnterprise) {
                setModalTitle(
                    saveAs
                        ? tt('enterpriseSaveMenu.saveSupertabAs')
                        : tt(rename ? 'enterpriseSaveModal.renameSupertab' : 'enterpriseContextMenu.saveSupertab')
                );
            } else {
                setModalTitle(
                    saveAs ? tt('saveMenu.savePageAs') : tt(rename ? 'saveModal.renamePage' : 'contextMenu.savePage')
                );
            }
        })();
    }, [tt, saveAs, rename, parentWindowName]);

    useEffect(() => {
        (async () => {
            const platform = getCurrentSync();
            const browser = platform.Browser.wrapSync({ name: parentWindowName, uuid: finUUID });
            let page: AttachedPage | Page = await browser.getPage(pageId);
            if (!page) {
                // page isn't attached to current window (e.g. Rename called from landing page)
                page = await platform.Storage.getPage(pageId);
            }
            setPageTitle(page.title);
        })();
    }, [pageId, modalInvocationId, parentWindowName]);

    return (
        <SaveModal
            clearErrorMessage={() => {
                setErrorMessage('');
            }}
            onSubmit={handleSubmit}
            onCancel={handleCancel}
            icon="StackIcon"
            placeholder={pageTitle}
            errorMessage={errorMessage}
            setErrorMessage={setErrorMessage}
            modalTitle={modalTitle}
        ></SaveModal>
    );
};

export default SavePage;
