import React from 'react';

import styled from 'styled-components';

import { Box, BoxProps } from '@openfin/ui-library';

import { ButtonConfig } from '@common/utils/menu-config';

import { Button } from '@enterprise/components/ModalContent/Button';
import { ResponseModalActionHandler } from '@enterprise/components/ModalContent/ResponseModal';

type ButtonGroupProps = {
    buttons: ButtonConfig[];
    align: 'left' | 'right';
    gap?: BoxProps['gap'];
    hasError?: boolean;
    onAction: ResponseModalActionHandler;
};

export const ButtonsRow = styled(Box)`
    margin-top: ${({ theme }) => theme.px.xsmall};
    justify-content: space-between;
    padding: 0 ${({ theme }) => theme.px.large} ${({ theme }) => theme.px.large} ${({ theme }) => theme.px.large};
`;

export const ButtonGroup: React.FC<ButtonGroupProps> = ({ buttons, align, gap, hasError, onAction }) => {
    return (
        <Box gap={gap ?? 'small'} role="group" aria-label={align}>
            {(buttons || []).map((config) => (
                <Button key={config.id} disabled={hasError} onAction={onAction} {...config} />
            ))}
        </Box>
    );
};
