import { useEffect, useState } from 'react';

import { finUUID } from '@common/utils/env';
import { showError, showSuccess } from '@common/utils/indicators/showIndicator';
import { ModalResponseEvent, waitForModalResponse } from '@common/utils/menu-window-provider';
import { getResponseModalBoundsAndCenterParentIfModalOffScreen } from '@common/utils/modal-bounds';
import { randomUUID } from '@common/utils/uuid';
import { getCurrentOFWindow } from '@common/utils/window';
import { saveWorkspaceInternal, updateWorkspaceInternal } from '@common/utils/workspace';
import { getOverwriteWorkspaceMenu } from '@common/utils/workspace-modals';

import { t, useTranslation } from '@common/api/i18next';

import { getCurrentSync } from '@client-platform/api';
import { BrowserWindowModule, Workspace } from '@client-platform/index';

import SaveModal from './SaveModal';

const saveWorkspace = async (title: string, workspace: Workspace, browser: BrowserWindowModule, saveAs: boolean) => {
    workspace.title = title;
    const platform = getCurrentSync();
    await saveWorkspaceInternal(workspace, platform);
    await platform.setActiveWorkspace(workspace);
    await showSuccess({
        message: saveAs
            ? t('successIndicator.workspaceSavedAs', { workspace: title })
            : t('successIndicator.workspaceSaved'),
        parentBrowserName: browser.identity.name
    });
};

const saveWorkspaceCopy = async (title: string, workspace: Workspace, browser: BrowserWindowModule) => {
    // copy of current workspace with provided title and new id
    const workspaceCopy = {
        ...workspace,
        title: title,
        workspaceId: randomUUID()
    };
    const platform = getCurrentSync();
    await saveWorkspaceInternal(workspaceCopy, platform);
    // set the copy as active workspace but don't apply it (to avoid existing windows closing)
    await platform.setActiveWorkspace(workspaceCopy);
    await showSuccess({
        message: t('successIndicator.workspaceSavedAs', { workspace: title }),
        parentBrowserName: browser.identity.name
    });
};

const renameWorkspace = async (newTitle: string, workspace: Workspace, browser: BrowserWindowModule) => {
    const platform = await getCurrentSync();
    // only save new title, not the workspace internals
    const savedWorkspace = await platform.Storage.getWorkspace(workspace.workspaceId);
    await updateWorkspaceInternal(
        {
            ...savedWorkspace,
            title: newTitle
        },
        platform
    );
    await platform.setActiveWorkspace({
        ...workspace,
        title: newTitle
    });
    await showSuccess({
        message: t('successIndicator.workspaceRenamed'),
        parentBrowserName: browser.identity.name
    });
};

const showOverwriteVerificationModal = async (title: string): Promise<{ data: ModalResponseEvent['data'] }> => {
    const { windowOptions, content } = await getOverwriteWorkspaceMenu(title);

    const { top, left } = await getResponseModalBoundsAndCenterParentIfModalOffScreen(windowOptions);

    const browserWindowCloseOptions = { ...windowOptions, top, left };

    const response = await waitForModalResponse({
        options: browserWindowCloseOptions,
        content
    });

    return response;
};

interface Props {
    saveAs?: boolean;
    rename?: boolean;
    parentWindowName: string;
    modalInvocationId?: number;
}

const SaveWorkspace = ({ parentWindowName, saveAs, rename, modalInvocationId }: Props) => {
    const [workspaceTitle, setWorkspaceTitle] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const { t: tt } = useTranslation();

    const handleCancel = async () => {
        setErrorMessage('');
    };

    const handleSubmit = async (title: string) => {
        const platform = getCurrentSync();
        const allSavedWorkspaces = await platform.Storage.getWorkspacesMetadata();
        const savedWorkspaceWithTitle = allSavedWorkspaces?.find((p) => p?.title === title);
        const currentWorkspace = await platform.getCurrentWorkspace();
        const isTitleExists =
            savedWorkspaceWithTitle && savedWorkspaceWithTitle.workspaceId !== currentWorkspace.workspaceId;
        const modalWindow = getCurrentOFWindow();

        await modalWindow.updateOptions({ customContext: isTitleExists ? 'error' : 'submitting' });

        if (rename && isTitleExists) {
            setErrorMessage(tt('saveModal.alreadyExists', { title }));
            return;
        }

        let shouldSaveCopy = saveAs;
        if (shouldSaveCopy && currentWorkspace.title === title) {
            // if we are in Save As modal but receive current workspace name then save without creating a copy
            shouldSaveCopy = false;
        }

        await modalWindow.hide();
        try {
            const browser = platform.Browser.wrapSync({ name: parentWindowName, uuid: finUUID });
            if (shouldSaveCopy) {
                // Save As
                if (savedWorkspaceWithTitle) {
                    const response = await showOverwriteVerificationModal(title);
                    if (response.data.actionName !== 'Replace') {
                        return;
                    }
                    await platform.Storage.deleteWorkspace(savedWorkspaceWithTitle.workspaceId);
                }
                await saveWorkspaceCopy(title, currentWorkspace, browser);
            } else if (rename) {
                await renameWorkspace(title, currentWorkspace, browser);
            } else {
                // regular save
                if (savedWorkspaceWithTitle) {
                    const response = await showOverwriteVerificationModal(title);
                    if (response.data.actionName !== 'Replace') {
                        return;
                    }
                }
                await saveWorkspace(title, currentWorkspace, browser, saveAs ?? false);
            }
        } catch (err) {
            console.error('SaveWorkspace: workspace failed to save: ', err);
            showError({
                message: tt('errorIndicator.workspaceSaveFailed'),
                parentBrowserName: parentWindowName
            });
        }
    };

    useEffect(() => {
        (async () => {
            const platform = getCurrentSync();
            const activeWorkspace = await platform.getCurrentWorkspace({ skipSnapshotUpdate: true });
            setWorkspaceTitle(activeWorkspace.title);
        })();
    }, [modalInvocationId, parentWindowName]);

    return (
        <SaveModal
            onSubmit={handleSubmit}
            onCancel={handleCancel}
            clearErrorMessage={() => {
                setErrorMessage('');
            }}
            icon="StackIcon"
            placeholder={workspaceTitle}
            errorMessage={errorMessage}
            setErrorMessage={setErrorMessage}
            modalTitle={
                saveAs
                    ? tt('saveMenu.saveWorkspaceAs')
                    : rename
                    ? tt('contextMenu.renameWorkspace')
                    : tt('contextMenu.saveWorkspace')
            }
        />
    );
};

export default SaveWorkspace;
