import { FC } from 'react';

import Head from 'next/head';

import { getUrlParams, Router, withRouter } from '@common/utils/router';

import Callout from '@enterprise/components/Callout/Callout';
import SavePage from '@enterprise/components/ModalContent/SavePage';
import SaveWorkspace from '@enterprise/components/ModalContent/SaveWorkspace';

import { SaveModalType } from '@client-platform/index';

interface Props {
    router: Router;
}

const SaveModalPopup: FC<Props> = ({ router }: Props) => {
    const { id, parentName, menuType, invocationId, isEnterprise: isParentEnterprise } = getUrlParams(router.asPath);
    const isEnterprise = isParentEnterprise === 'true';

    const getFormBySaveType = () => {
        const saveType = menuType as SaveModalType;
        switch (saveType) {
            case SaveModalType.SAVE_PAGE:
            case SaveModalType.SAVE_PAGE_AS:
            case SaveModalType.RENAME_PAGE:
                return (
                    <SavePage
                        pageId={id}
                        parentWindowName={parentName}
                        saveAs={saveType === SaveModalType.SAVE_PAGE_AS}
                        rename={saveType === SaveModalType.RENAME_PAGE}
                        modalInvocationId={Number(invocationId)}
                        isEnterprise={isEnterprise}
                    />
                );
            case SaveModalType.SAVE_WORKSPACE:
            case SaveModalType.SAVE_WORKSPACE_AS:
            case SaveModalType.RENAME_WORKSPACE:
                return (
                    <SaveWorkspace
                        parentWindowName={parentName}
                        modalInvocationId={Number(invocationId)}
                        saveAs={saveType === SaveModalType.SAVE_WORKSPACE_AS}
                        rename={saveType === SaveModalType.RENAME_WORKSPACE}
                    />
                );
            default:
                console.warn('Unsupported save modal type');
                return <></>;
        }
    };

    return (
        <>
            <Head>
                <title>Browser Save Workspace/Page Popup</title>
            </Head>
            <Callout isEnterprise={isEnterprise}>{getFormBySaveType()}</Callout>
        </>
    );
};

export default withRouter(SaveModalPopup);
